body {
  margin: 0;
  background-color: #f5f5f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@font-face {
  font-family:'Vogue';
  src: url('/public/font/Vogue Regular.eot');
  src: url('/public/font/Vogue Regular.eot?#iefix') format('embedded-opentype'),
  url('/public/font/Vogue Regular.woff2') format('woff2'),
  url('/public/font/Vogue Regular.woff') format('woff'),
  url('/public/font/Vogue Regular.svg#Vogue Regular') format('svg');
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  unicode-range: U+0020-00FE;
}

@font-face {
  font-family:'LEMON MILK Bold';
  src: url('/public/font/LEMON MILK Bold.eot');
  src: url('/public/font/LEMON MILK Bold.eot?#iefix') format('embedded-opentype'),
