  url('/public/font/LEMON MILK Bold.woff') format('woff'),
  url('/public/font/LEMON MILK Bold.svg#LEMON MILK Bold') format('svg');
  font-weight: 700;
  font-style: normal;
  font-stretch: normal;
  unicode-range: U+0020-00F7;
}

